.work img {
    width: 100%;
    margin: 0;
    z-index: 2;
    position: relative;
    transition: all 0.3s ease-in;
}

.work:hover img {
    /* top: -10px; */
    z-index: 0;
    opacity: 0.4;
    cursor: pointer;
    /* position: absolute; */
    transition: all 0.6s ease-out;
}

