.viewButton {
    z-index: 1;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 25%;
    height: 12%;
    transform: translate(-50%, -50%);
    /* background-color: rgb(214, 214, 214); */
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    color: white;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 1em;
    text-align: center;
    /* border: 1px solid grey; */
}

.work h3 {
    /* position: absolute; */
    /* bottom: 0; */
    margin-left: 10px;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    color: black;
    text-decoration: none;
}

